import styled, { css } from 'styled-components';

import {
  defaultThemeProp,
  Text,
  MetaList,
  Link,
  Flex,
  StyledPopover,
  calculateFontSize
} from '@pega/cosmos-react-core';

import { SearchResultProps } from './SearchResults.types';

const StyledSearchResult = styled.article(({ theme }) => {
  const fontSize = calculateFontSize(theme.base['font-size'], theme.base['font-scale']);

  return css`
    h2 ${StyledPopover} {
      font-size: ${fontSize.s};
      font-weight: ${theme.base['font-weight'].normal};
    }
  `;
});

StyledSearchResult.defaultProps = defaultThemeProp;

const StyledDescription = styled(Text)`
  word-break: break-word;
`;

const SearchResult = ({
  id,
  title,
  link,
  description,
  meta = []
}: SearchResultProps): JSX.Element => {
  return (
    <Flex as={StyledSearchResult} container={{ direction: 'column', gap: 1 }}>
      <Flex container={{ direction: 'column-reverse' }}>
        <Text variant='h2'>
          <Link {...link}>{title}</Link>
        </Text>
        <Text variant='secondary' as='div'>
          {id}
        </Text>
      </Flex>
      {description && <StyledDescription forwardedAs='p'>{description}</StyledDescription>}
      {!!meta.length && <MetaList items={meta} />}
    </Flex>
  );
};

export default SearchResult;
