import { useState } from 'react';

import {
  Button,
  Checkbox,
  CheckboxGroup,
  ExpandCollapse,
  Flex,
  Icon,
  registerIcon,
  RadioButton,
  RadioButtonGroup,
  Text,
  useI18n
} from '@pega/cosmos-react-core';
import * as caretDownIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/caret-down.icon';

import { FilterProps, SearchResultsProps, SelectFilterItemProps } from './SearchResults.types';
import { StyledFilter, StyledFilterButton } from './SearchResults.styles';

registerIcon(caretDownIcon);

interface FilterComponentProps {
  onFilterChange?: SearchResultsProps['onFilterChange'];
  onClearFilter?: SearchResultsProps['onClearFilter'];
}

const Filter = ({
  onFilterChange,
  onClearFilter,
  name,
  expanded = true,
  ...control
}: FilterProps & FilterComponentProps): JSX.Element => {
  const t = useI18n();
  const [filterExpanded, setFilterExpanded] = useState(expanded);

  let filterControl;
  let filterActive = false;
  // eslint-disable-next-line sonarjs/no-small-switch
  switch (control.type) {
    case 'select': {
      const { selectType, items } = control;
      let SelectFilterGroup = CheckboxGroup;
      let SelectFilter = Checkbox;
      if (selectType === 'single-select') {
        SelectFilterGroup = RadioButtonGroup;
        SelectFilter = RadioButton;
      }
      filterControl = (
        <SelectFilterGroup
          label={name}
          labelHidden
          onChange={e => {
            const item = items.find((x: SelectFilterItemProps) => x.id === e.target.id);
            if (!item) return;
            const newItem = { ...item, checked: !item.checked };

            onFilterChange?.({
              name,
              type: control.type,
              value: newItem
            });
          }}
        >
          {control.items.map((filterOption: SelectFilterItemProps) => {
            if (filterOption.checked) filterActive = true;
            return (
              <SelectFilter
                key={filterOption.id}
                id={filterOption.id}
                name={filterOption.name}
                label={filterOption.label}
                checked={filterOption.checked}
              />
            );
          })}
        </SelectFilterGroup>
      );
      break;
    }
    default:
      filterControl = null;
  }

  return (
    <StyledFilter>
      <Flex container={{ justify: 'between' }}>
        <Flex
          as={StyledFilterButton}
          container={{ gap: 1, alignItems: 'center' }}
          variant='text'
          onClick={() => setFilterExpanded(curr => !curr)}
          aria-expanded={filterExpanded}
          aria-label={t(filterExpanded ? 'collapse_noun' : 'expand_noun', [name])}
        >
          <Icon name='caret-down' />
          <Text variant='h3' as='span'>
            {name}
          </Text>
        </Flex>
        {filterActive && (
          <Button variant='link' onClick={() => onClearFilter?.(name)}>
            {t('clear')}
          </Button>
        )}
      </Flex>
      <ExpandCollapse collapsed={!filterExpanded}>{filterControl}</ExpandCollapse>
    </StyledFilter>
  );
};

export default Filter;
