import {
  forwardRef,
  useContext,
  useRef,
  FunctionComponent,
  PropsWithoutRef,
  ComponentProps
} from 'react';

import {
  Button,
  Card,
  CardContent,
  Count,
  Flex,
  ForwardProps,
  Grid,
  Icon,
  registerIcon,
  Modal,
  ModalMethods,
  ModalManagerContext,
  EmptyState,
  SearchInput,
  Selectable,
  Text,
  useI18n,
  useBreakpoint,
  useAfterInitialEffect
} from '@pega/cosmos-react-core';
import * as filterIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/filter.icon';
import * as filterOnIcon from '@pega/cosmos-react-core/lib/components/Icon/icons/filter-on.icon';

import {
  SearchResultsProps,
  SearchResultProps,
  FilterProps,
  ActiveFilters
} from './SearchResults.types';
import SearchResult from './SearchResult';
import Filter from './Filter';
import {
  StyledModalFilterContent,
  StyledCountText,
  StyledSelectedFilters,
  StyledSearchResults,
  StyledFilterTitle
} from './SearchResults.styles';

registerIcon(filterIcon, filterOnIcon);

const FilterModal = (
  props: Pick<
    SearchResultsProps,
    'count' | 'filters' | 'onFilterChange' | 'onClearFilter' | 'onClearAllFilters'
  > & {
    activeFilters: ActiveFilters[];
  }
) => {
  const { activeFilters, count, filters, onFilterChange, onClearFilter, onClearAllFilters } = props;
  const t = useI18n();

  return (
    <Modal heading={t('filters')} autoWidth>
      <Grid as={StyledModalFilterContent} container={{ rowGap: 2 }}>
        <Flex container={{ gap: 1, alignItems: 'center' }}>
          <Text as={StyledCountText} variant='secondary'>
            {t('results_count', [count], { count })}
          </Text>
          {onClearAllFilters && !!activeFilters.length && (
            <Button variant='link' onClick={onClearAllFilters}>
              {t('clear_all')}
            </Button>
          )}
        </Flex>
        {!!activeFilters.length && (
          <Flex as={StyledSelectedFilters} container={{ gap: 1 }}>
            {activeFilters.map(filter => {
              return (
                <Selectable
                  key={filter.id}
                  id={filter.id}
                  onRemove={() =>
                    onFilterChange?.({
                      name: filter.groupName,
                      type: filter.type,
                      value: filter.resetValue
                    })
                  }
                >
                  {`${filter.groupName}: ${filter.name}`}
                </Selectable>
              );
            })}
          </Flex>
        )}
        <div>
          {filters.map((filter: FilterProps) => {
            return (
              <Filter
                key={filter.name}
                onFilterChange={onFilterChange}
                onClearFilter={onClearFilter}
                {...filter}
              />
            );
          })}
        </div>
      </Grid>
    </Modal>
  );
};

const SearchResults: FunctionComponent<SearchResultsProps & ForwardProps> = forwardRef(
  (
    {
      results,
      searchInputProps,
      count,
      filters,
      onFilterChange,
      onClearAllFilters,
      onClearFilter,
      onShowMore,
      ...restProps
    }: PropsWithoutRef<SearchResultsProps>,
    ref: SearchResultsProps['ref']
  ) => {
    const t = useI18n();
    const isMedium = useBreakpoint('md');
    const { create } = useContext(ModalManagerContext);
    const filtersMethods = useRef<ModalMethods<ComponentProps<typeof FilterModal>>>();
    const { placeholder = t('search_placeholder_default') } = searchInputProps;

    const activeFilters: ActiveFilters[] = filters.flatMap(group => {
      if (group.type === 'select') {
        return group.items.flatMap(item => {
          if (item.checked) {
            return {
              id: item.id,
              name: item.name,
              resetValue: { ...item, checked: false },
              groupName: group.name,
              type: group.type
            };
          }
          return [];
        });
      }
      return [];
    });

    const openFiltersModal = () => {
      filtersMethods.current = create(FilterModal, {
        activeFilters,
        count,
        filters,
        onFilterChange,
        onClearFilter,
        onClearAllFilters
      });
    };

    useAfterInitialEffect(() => {
      if (filtersMethods.current) {
        filtersMethods.current.update({
          activeFilters,
          count,
          filters
        });
      }
    }, [activeFilters, count, filters]);

    useAfterInitialEffect(() => {
      if (isMedium && filtersMethods.current) filtersMethods.current.dismiss();
    }, [isMedium]);

    return (
      <Card>
        <CardContent>
          <Grid
            {...restProps}
            as={StyledSearchResults}
            ref={ref}
            md={
              filters.length
                ? {
                    container: {
                      cols: '15rem auto',
                      areas: '"search search" "filters results"'
                    }
                  }
                : {}
            }
            s={
              filters.length
                ? {
                    container: {
                      cols: 'auto'
                    }
                  }
                : {}
            }
            container={{ rows: '1fr auto', areas: '"search" "results"', rowGap: 2, colGap: 4 }}
          >
            <Grid item={{ area: 'search' }}>
              <SearchInput {...searchInputProps} placeholder={placeholder} />
            </Grid>
            {isMedium && !!filters.length && (
              <Grid item={{ area: 'filters', alignSelf: 'start' }}>
                <Flex as={StyledFilterTitle} container={{ justify: 'between' }}>
                  <Text variant='h2'>{t('filters')}</Text>
                  {onClearAllFilters && !!activeFilters.length && (
                    <Button variant='link' onClick={onClearAllFilters}>
                      {t('clear_all')}
                    </Button>
                  )}
                </Flex>
                {filters.map((filter: FilterProps) => {
                  return (
                    <Filter
                      key={filter.name}
                      onFilterChange={onFilterChange}
                      onClearFilter={onClearFilter}
                      {...filter}
                    />
                  );
                })}
              </Grid>
            )}
            <Grid
              item={{ area: 'results' }}
              container={{
                alignItems: 'center',
                rowGap: 2,
                rows: 'auto 1fr',
                areas: '"meta" "items"'
              }}
            >
              <Grid item={{ area: 'meta' }} container={{ rowGap: 1 }}>
                <Flex container={{ justify: 'between', alignItems: 'center' }}>
                  <Text as={StyledCountText} variant='secondary'>
                    {searchInputProps.value !== '' || results.length > 0
                      ? t('results_count', [results.length], { count: results.length })
                      : ''}
                  </Text>
                  {!isMedium && !!filters.length && (
                    <Button variant='simple' onClick={openFiltersModal}>
                      <Icon name={activeFilters.length > 0 ? 'filter-on' : 'filter'} />
                      &nbsp;{t('filters')}&nbsp;
                      <Count>{activeFilters.length}</Count>
                    </Button>
                  )}
                </Flex>
                {!!activeFilters.length && (
                  <Flex as={StyledSelectedFilters} container={{ gap: 1 }}>
                    {activeFilters.map(filter => {
                      return (
                        <Selectable
                          key={filter.id}
                          id={filter.id}
                          onRemove={() =>
                            onFilterChange?.({
                              name: filter.groupName,
                              type: filter.type,
                              value: filter.resetValue
                            })
                          }
                        >
                          {`${filter.groupName}: ${filter.name}`}
                        </Selectable>
                      );
                    })}
                  </Flex>
                )}
              </Grid>
              <Grid
                item={{ area: 'items', alignSelf: results.length ? 'start' : 'center' }}
                container={{ rowGap: 4 }}
              >
                {results.length ? (
                  results.map((result: SearchResultProps) => {
                    return <SearchResult key={result.id} {...result} />;
                  })
                ) : (
                  <Grid item={{ alignSelf: 'stretch' }}>
                    <EmptyState
                      message={searchInputProps.value === '' ? t('empty_search') : undefined}
                    />
                  </Grid>
                )}
              </Grid>
              {onShowMore && (
                <Flex container={{ justify: 'center' }}>
                  <Button variant='link' onClick={onShowMore}>
                    {t('show_more')}
                  </Button>
                </Flex>
              )}
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    );
  }
);

export default SearchResults;
